.container {
  width: 100%;
  background-image: url("https://github.com/mdab4793/shop/blob/main/background/9.jpeg?raw=true");
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: center;
  font-family: "Orbitron", sans-serif;
}

.main {
  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: space-around;
  flex-wrap: wrap;
}
.section {
  width: 33%;
  margin: 50px;
}
.section img {
  transition: all 0.2s linear;
}
.section:hover img {
  transform: scale(1.03);
}
.wrapper {
  width: 100%;
  color: wheat;
  font-family: "Orbitron", sans-serif;
  background: none;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 20px;
}
.wrapper img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-radius: 20px;
}
.wrapper :hover {
  color: gray;
}

.wrapper h3 {
  padding: 40px;
  font-size: 35px;
}

.wrapper p {
  font-size: 30px;
  padding: 30px;
}

.cart {
  width: 20%;
  height: 35px;
  border-radius: 15px;
  color: black;
  margin-bottom: 10px;
  background-color: wheat;
  border: none;
}
.cart:hover {
  color: cornflowerblue;
}

.price button {
  margin-top: 20px;
  background: black;
  color: wheat;
  border: none;
  padding: 13px;
  border-radius: 10px;
  font-family: "Orbitron", sans-serif;
  position: relative;
  left: 45%;
}

@media screen and (max-width: 1200px) {
  .container {
    width: 100%;
  }
  .price button {
    background: black;
    color: wheat;
    border: none;
    padding: 13px;
    border-radius: 10px;
    font-family: "Orbitron", sans-serif;
    position: relative;
    left: 40%;
  }
  .wrapper {
    width: 100%;
  }
  .wrapper h3 {
    padding: 40px;
    font-size: 20px;
  }
  .wrapper p {
    padding: 40px;
    font-size: 20px;
  }
}

@media screen and (max-width: 768px) {
  .container {
    width: 100%;
  }
  .wrapper img {
    width: 100%;
    height: 450px;
  }
  .price button {
    background: black;
    color: wheat;
    border: none;
    padding: 13px;
    border-radius: 10px;
    font-family: "Orbitron", sans-serif;
    position: relative;
    left: 30%;
  }
  .wrapper h3 {
    padding: 40px;
    font-size: 10px;
  }
  .wrapper p {
    padding: 40px;
    font-size: 16px;
  }
}
@media only screen and (min-device-width: 375px) and (max-device-width: 479px) {
}
